* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1, h2, h3 {
  color: rgba(40,40,40, 1.0)
}

body {
  font-family: 'Lato', sans-serif;
}

h3, h4 {
  font-weight: 700;
  color: rgba(100, 100 ,100 , 1.0);
}

.App {
  transition: all 0.5s ease;
}

.library-active {
  margin-left: 30%;
}

@media screen and (max-width: 768px){
  .library-active {
    margin-left: 20%;
  }
}

@import './player';
@import './song';
@import './library';
@import './nav';