.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.time-control {
  width: 50%;
  display: flex;
  align-items: center;

  .track {
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
    background-color: blue;
  }

  .animate-track {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
    background-color: lightgray;
  }

  input {
    width: 100%;
   -webkit-appearance: none;
   background: transparent;
   cursor: pointer;

   &:focus{
     outline: none;
   }

   &::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 16px;
    height: 16px;
   }

   &::-moz-range-thumb {
    -webkit-appearance: none;
    background: transparent;
    border: none;
   }
  }
  
  p {
    padding: 1rem;
  }
}

.play-control {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;
  padding: 1rem;

  svg {
    cursor: pointer;
  }
}

@media screen and (max-width: 768px) {
  .time-control {
    width: 90%;
  }
  .play-control {
    width: 60%;
  }
}